import { Lightning } from "@lightningjs/sdk";
import Button from "./button.js";

const rowLength = 3;
const colLength = 3;
const numAppsPerPage = rowLength * colLength;

export default class AppTest extends Lightning.Component {
  static _template() {
    return {
      ButtonList: {
        Button1: { type: Button, label: "1", x: 40, y: 300 },
        Button2: { type: Button, label: "2", x: 660, y: 300 },
        Button3: { type: Button, label: "3", x: 1280, y: 300 },
        Button4: { type: Button, label: "4", x: 40, y: 450 },
        Button5: { type: Button, label: "5", x: 660, y: 450 },
        Button6: { type: Button, label: "6", x: 1280, y: 450 },
        Button7: { type: Button, label: "7", x: 40, y: 600 },
        Button8: { type: Button, label: "8", x: 660, y: 600 },
        Button9: { type: Button, label: "9", x: 1280, y: 600 },
        Button0: {
          type: Button,
          label: "NEXT",
          x: 40,
          y: 750,
          width: 1840,
          style: "bold"
        }
      }
    };
  }

  _firstEnable() {
    this.numButtons = this.tag("ButtonList").children.length;
    this.nextIdx = this.numButtons - 1;
    this.numInstalledApps = 0;
    this.installedAppNames = undefined;
  }

  _enable() {
    this.buttonIdx = 0;
    this.pageIdx = 0;

    this.thunderWrapper
      .getInstalledAppList()
      .then(
        result => {
          console.log(JSON.stringify(result));
          this.installedAppNames = result;
          this.numInstalledApps = this.installedAppNames.length;
          this.updateButtonLabels();
        },
        error => {
          console.log("AppTest: enable() - " + error);
        }
      )
      .catch(err => {
        console.log(err);
      });
  }

  _getFocused() {
    return this.tag("ButtonList").children[this.buttonIdx];
  }

  _handleLeft() {
    if (this.buttonIdx === this.nextIdx) {
      this.buttonIdx -= rowLength;
    } else {
      if (this.buttonIdx % rowLength > 0) {
        this.buttonIdx--;
      }
    }
  }

  _handleRight() {
    if (this.buttonIdx === this.nextIdx) {
      this.buttonIdx -= 1;
    } else {
      if (this.buttonIdx % rowLength < rowLength - 1) {
        this.buttonIdx++;
      }
    }
  }

  _handleUp() {
    if (this.buttonIdx === this.nextIdx) {
      this.buttonIdx -= Math.ceil(rowLength / 2);
    } else {
      if (this.buttonIdx >= rowLength) {
        this.buttonIdx -= rowLength;
      }
    }
  }

  _handleDown() {
    if (this.buttonIdx != this.nextIdx) {
      if (this.buttonIdx >= (colLength - 1) * rowLength) {
        this.buttonIdx = this.nextIdx;
      } else {
        this.buttonIdx += rowLength;
      }
    }
  }

  _handleCenter() {
    this.buttonAction(this.buttonIdx);
  }

  _handleZero() {
    this.buttonAction(9);
  }

  _handleOne() {
    this.buttonAction(0);
  }

  _handleTwo() {
    this.buttonAction(1);
  }

  _handleThree() {
    this.buttonAction(2);
  }

  _handleFour() {
    this.buttonAction(3);
  }

  _handleFive() {
    this.buttonAction(4);
  }

  _handleSix() {
    this.buttonAction(5);
  }

  _handleSeven() {
    this.buttonAction(6);
  }

  _handleEight() {
    this.buttonAction(7);
  }

  _handleNine() {
    this.buttonAction(8);
  }

  updateButtonLabels() {
    console.log(
      "updateButtonLabels() - installedAppNames: " +
        this.installedAppNames
    );
    if (this.installedAppNames != undefined) {
      for (var i = 0; i < numAppsPerPage; ++i) {
        var label = this.installedAppNames[
          this.pageIdx * numAppsPerPage + i
        ].split(".").pop();
        this.tag("Button" + (i + 1)).updateLabel(
          label === undefined ? "" : label
        );
      }
    }
  }

  buttonAction(activeButtonIdx) {
    console.log("buttonAction() " + activeButtonIdx + " " + this.nextIdx);
    if (activeButtonIdx === this.nextIdx) {
      this.nextPage();
      this.updateButtonLabels();
    } else {
      if (
        activeButtonIdx + numAppsPerPage * this.pageIdx <
        this.numInstalledApps
      ) {
        console.log("buttonAction() - launching app");
        this.launchApp(activeButtonIdx);
      }
    }
  }

  nextPage() {
    console.log("nextPage() " + this.pageIdx);
    console.log(
      "nextPage() - " +
        this.numInstalledApps +
        " " +
        numAppsPerPage +
        " " +
        Math.ceil(this.numInstalledApps / numAppsPerPage)
    );
    this.pageIdx =
      ++this.pageIdx % Math.ceil(this.numInstalledApps / numAppsPerPage);
    console.log("nextPage() " + this.pageIdx);
  }

  launchApp(activeButtonIdx) {
    var appName = this.installedAppNames[
      activeButtonIdx + numAppsPerPage * this.pageIdx
    ];
    this.thunderWrapper.launchApp(appName).then(
      function(result) {
        console.log("AppTest: launchApp() - " + appName + " " + result);
      },
      function(error) {
        console.log("AppTest: launchApp() - " + appName + " " + error);
      }
    );
  }

  configure(thunderWrapper) {
    this.thunderWrapper = thunderWrapper;
  }
}
