import { Lightning } from "@lightningjs/sdk"
import Button from "./button.js";

export default class DisplaySettings extends Lightning.Component {
  static _template() {
    return {
      ButtonList: {
        alpha: this.bindProp("buttonListAlpha"),
        Button1: { type: Button, label: "1:Primary \n 2:Secondary",
          labelY: 110, x: 40, y: 300, h: 220 },
        Button2: { type: Button, label: "1:Secondary \n 2:Primary",
          labelY: 110, x: 660, y: 300, h: 220 }
      }
    };
  }

  configure(thunderWrapper) {
    this.thunderWrapper = thunderWrapper
  }

  _firstEnable() {
    this.numButtons = this.tag("ButtonList").children.length;
    this.buttonIdx = 0;
  }

  _getFocused() {
    return this.tag("ButtonList").children[this.buttonIdx];
  }

  _handleLeft() {
    if (this.buttonIdx > 0) {
      this.buttonIdx -= 1;
    }
  }

  _handleRight() {
    if (this.buttonIdx < (this.numButtons - 1) ) {
      this.buttonIdx += 1;
    }
  }

  _handleCenter() {
    this.buttonAction();
  }

  async buttonAction() {
    switch (this.buttonIdx) {
      case 0:
        await this.button0Action();
        break;
      case 1:
        await this.button1Action();
        break;
      default:
        break;
    }
  }

  async button0Action() {
    await this.thunderWrapper.setDisplays({
      "displays": [{"id": 1, "display": "westeros-asplayer-0"}, {"id": 2, "display": "westeros-asplayer-1"}]})
  }

  async button1Action() {
    await this.thunderWrapper.setDisplays({
      "displays": [{"id": 1, "display": "westeros-asplayer-1"}, {"id": 2, "display": "westeros-asplayer-0"}]})
  }

}
