import { Lightning } from '@lightningjs/sdk'

export default class Button extends Lightning.Component {
  static _template() {
    return {
      w: 600,
      h: 100,
      color: 0xff616161,
      texture: Lightning.Tools.getRoundRect(600, 100, 4),
      Label: {
        x: w => w / 2,
        y: 52,
        mount: 0.5,
        color: 0xffcacaca,
        text: { fontSize: 64, fontStyle: 'italic' },
      },
    }
  }

  _firstEnable() {
    if (this.label) {
      this.tag('Label').patch({ text: { text: this.label } })
    }

    if (this.style) {
      this.tag('Label').patch({ text: { fontStyle: this.style } })
    }

    if (this.labelY) {
      this.tag('Label').patch({ y: this.labelY })
    }

    if (this.width) {
      this.tag('Label').patch({ x: this.width / 2 })
      this.patch({ w: this.width })
    }

    if (this.fontSize) {
      this.tag('Label').patch({ text: { fontSize: this.fontSize } })
    }

    if (this.buttonTexture) {
      this.patch({ texture: this.buttonTexture })
    }
  }
  _focus() {
    this.color = 0xffababab
    this.tag('Label').color = 0xff333333
  }
  _unfocus() {
    this.color = 0xff616161
    this.tag('Label').color = 0xffcacaca
  }

  updateLabel(label) {
    this.label = label
    this.tag('Label').patch({ text: { text: label } })
  }

  getLabel() {
    return this.label
  }
}
