import DemoExperience from "./demo-experience";
import AppExperience from "./app-experience";
import ErrorExperience from "./error-experience";

export function getRoutes(injectDependencies)
{
  return {
    root: "home",
    routes: [
      {
        path: "home",
        component: DemoExperience,
        on: injectDependencies.bind(null, "home"),
        widgets: ["MuteControlOverlay"],
      },
      {
        path: "app/:pkg",
        component: AppExperience,
        on: injectDependencies.bind(null, "app/:pkg"),
        widgets: ["MuteControlOverlay"],
      },
      {
        path: "!",
        component: ErrorExperience
      }
    ],
  };
}
