import { Lightning } from "@lightningjs/sdk"
import { Mode, Resolution } from "./video-player.js"
import Button from "./button.js";

const greenScreenDemoAppName = "uk.sky.cherry.greenscreendemo"
const launcherName = "uk.sky.cherry.launcher"

export default class DeepLinkTest extends Lightning.Component {
  static _template() {
    return {
      ButtonList: {
        alpha: this.bindProp("buttonListAlpha"),
        Button1: { type: Button, label: "Valid Deep Link \n Vertical View",
          labelY: 110, x: 40, y: 300, h: 220 },
        Button2: { type: Button, label: "Invalid Deep Link \n Horizontal View",
          labelY: 110, x: 660, y: 300, h: 220 },
        Button3: { type: Button, label: "Invalid Deep Link \n Horizontal View \n with component",
          labelY: 110, x: 1280, y: 300, h: 220 },
      }
    };
  }

  configure(thunderWrapper, avCastPlayer) {
    this.thunderWrapper = thunderWrapper
    this.avCastPlayer = avCastPlayer
  }

  _firstEnable() {
    this.numButtons = this.tag("ButtonList").children.length;
    this.buttonIdx = 0;
    this.initialized = false;
    this.buttonListAlpha = 1;
  }

  async _disable() {
    this.showButtons();
    this.initialized = false;
    await this.startApp(launcherName);
    await this.closeApp(greenScreenDemoAppName);
    await this.avCastPlayer.stop();
  }

  _getFocused() {
    return this.tag("ButtonList").children[this.buttonIdx];
  }

  _handleLeft() {
    this.showButtons();

    if (this.buttonIdx > 0) {
      this.buttonIdx -= 1;
    }
  }

  _handleRight() {
    this.showButtons();

    if (this.buttonIdx < (this.numButtons - 1) ) {
      this.buttonIdx += 1;
    }
  }

  _handleCenter() {
    if (this.buttonListAlpha == 0) {
      this.showButtons();
      return;
    }

    this.buttonAction();
    this.hideButtons();
  }

  showButtons() {
    this.buttonListAlpha = 1;
  }

  hideButtons() {
    this.buttonListAlpha = 0;
  }

  async buttonAction() {
    if (!this.initialized) {
      await this.initialiseApp();
    }

    switch (this.buttonIdx) {
      case 0:
        await this.button0Action();
        break;
      case 1:
        await this.button1Action();
        break;
      case 2:
        await this.button2Action();
        break;
      default:
        break;
    }
  }

  async cooldown(ms) {
    return new Promise(resolve => setTimeout(resolve, ms))
  }

  async initialiseApp() {
    await this.startApp(greenScreenDemoAppName);
    await this.avCastPlayer.stop();
    await this.cooldown(200);
    await this.startPlayer();
    this.initialized = true;
  }

  async startApp(appName, dataUri, componentName) {
    try {
      await this.thunderWrapper.launchApp(appName, dataUri, componentName);
    } catch (e) {
      console.error("DeepLinkTest: Error while launching app:", e);
    }
  }

  async closeApp(appName) {
    try {
      await this.thunderWrapper.closeApp(appName, true);
    } catch (e) {
      console.error("DeepLinkTest: Error while closing app:", e);
    }
  }


  async startPlayer(playbackRegion) {
    console.log("DeepLinkTest: startPlayer")
    if (!this.cherryIP || !this.videoPort) {
        let [cherryIP, videoPort] = await this.thunderWrapper.getCastConnectionStatus()
        this.cherryIP = cherryIP
        this.videoPort = videoPort
    }
    let result = await this.avCastPlayer.start(
      this.cherryIP, this.videoPort, Mode.TEXTURE, Resolution.RES_1920x1080, playbackRegion, false)
    if (!result.success) {
      throw new Error("Failed to start player.");
    }
  }

  async button0Action() {
    await this.startApp(
      greenScreenDemoAppName,
      "gsd://greenscreendemo/test?view=SIDEBAR_VERTICAL");
  }

  async button1Action() {
    await this.startApp(
      greenScreenDemoAppName,
      "http://greenscreendemo/test?view=SIDEBAR_HORIZONTAL");
  }

  async button2Action() {
    await this.startApp(
      greenScreenDemoAppName,
      "http://greenscreendemo/test?view=SIDEBAR_HORIZONTAL",
      "uk.sky.cherry.greenscreendemo/uk.sky.cherry.greenscreendemo.MainActivity");
  }
}
