import { Lightning } from "@lightningjs/sdk";
import Button from "./button.js";
import SkyASPlayer from "./sky-as-player";

export default class SkyASTest extends Lightning.Component {
  static _template() {
    return {
      ButtonList: {
        ButtonSkyNews: {
          type: Button,
          label: "Sky News",
          x: 100,
          y: 60,
          asParams: {
            uri:
              "https%3A%2F%2Flinear019-gb-hls1-prd-ak.cdn.skycdp.com/100e/Content/HLS_001_sd/Live/channel(skynews)/index_mob.m3u8",
            format: "HLS"
          }
        },
        ButtonSkyCinemaFamily: {
          type: Button,
          label: "Sky Cinema Family",
          x: 100,
          y: 200,
          asParams: {
            uri:
              "https%3A%2F%2Flin013-gb-s8-prd-ak.cdn01.skycdp.com/v1/frag/bmff/enc/cenc/t/SCINFAH_HD_SU_SKYUK_4018_0_5576146500146789163.mpd",
            format: "HLS"
          }
        },
        ButtonSkyFootball: {
          type: Button,
          label: "Sky Football",
          x: 100,
          y: 340,
          asParams: {
            uri:
              "https%3A%2F%2Flin005-gb-s8-prd-ak.cdn01.skycdp.com/v1/frag/bmff/enc/cenc/t/SSPOFHD_HD_SU_SKYUK_3939_0_8930469780534566163.mpd",
            format: "HLS"
          }
        }
      }
    };
  }

  _firstEnable() {
    this.skyASPlayer = new SkyASPlayer(this.thunderWrapper);
    this.numButtons = this.tag("ButtonList").children.length;
    this.canvas = document.getElementsByTagName("canvas")[0];
    this.playing = false;
  }

  _enable() {
    this.buttonIdx = 0;
  }

  async _disable() {
    if (this.playing) {
      await this.stopStream();
    }
  }

  _getFocused() {
    return this.tag("ButtonList").children[this.buttonIdx];
  }

  _handleUp() {
    if (this.buttonIdx > 0) {
      this.buttonIdx--;
    }
  }

  _handleDown() {
    if (this.buttonIdx < this.numButtons - 1) {
      this.buttonIdx++;
    }
  }

  async _handleCenter() {
    let asParams = this.tag("ButtonList").children[this.buttonIdx].asParams;
    this.playing = await this.watchSteam(asParams);
    if (this.playing) {
      this.canvas.style.visibility = "hidden";
    }
  }

  async _handlePlayPause() {
    if (this.playing) {
      await this.stopStream();
      this.playing = false;
    }
  }

  async watchSteam(asParams) {
    return await this.skyASPlayer.watchStream(asParams);
  }

  async stopStream() {
    await this.skyASPlayer.stop();
    this.canvas.style.visibility = "visible";
  }

  configure(thunderWrapper) {
    this.thunderWrapper = thunderWrapper;
  }
}
