import { Lightning } from "@lightningjs/sdk"
import Button from "./button.js";
import { Type } from "./video-player.js"

export default class CastSettings extends Lightning.Component {
  static _template() {
    return {
      ButtonList: {
        alpha: this.bindProp("buttonListAlpha"),
        Button1: { type: Button, label: "AVCast",
          labelY: 110, x: 40, y: 300, h: 220 },
        Button2: { type: Button, label: "HDMI",
          labelY: 110, x: 660, y: 300, h: 220 }
      }
    };
  }

  configure(videoPlayer) {
    this.videoPlayer = videoPlayer
  }

  _firstEnable() {
    this.numButtons = this.tag("ButtonList").children.length;
    this.buttonIdx = 0;
  }

  _getFocused() {
    return this.tag("ButtonList").children[this.buttonIdx];
  }

  _handleLeft() {
    if (this.buttonIdx > 0) {
      this.buttonIdx -= 1;
    }
  }

  _handleRight() {
    if (this.buttonIdx < (this.numButtons - 1) ) {
      this.buttonIdx += 1;
    }
  }

  _handleCenter() {
    this.buttonAction();
  }

  async buttonAction() {
    switch (this.buttonIdx) {
      case 0:
        await this.button0Action();
        break;
      case 1:
        await this.button1Action();
        break;
      default:
        break;
    }
  }

  async button0Action() {
    console.log("Cast settings: setting cast type to AVCast");
    await this.videoPlayer.setCastType(Type.IP)
  }

  async button1Action() {
    console.log("Cast settings: setting cast type to HDMI");
    await this.videoPlayer.setCastType(Type.HDMI)
  }

}
