import {Lightning} from "@lightningjs/sdk";

export default class ErrorExperience extends Lightning.Component {
  _init() {
    this.errorObj = "Something went wrong."
  }

  static _template() {
    return {
      x: 0,
      y: 0,
      w: 1920,
      h: 1080,
      color: 0xff000000,
      rect: true,
      alpha: 1,
      Message: {
        x: (w => w/2),
        y: (h => h/2),
        mount: 0.5,
        text: {
          text: this.bindProp("errorObj"),
          color: 0xff20ba81,
          fontSize: 35
        },
      },
    }
  }

  set params(args) {
    console.log("Setting error object:",  args.request.error)
    this.errorObj = args.request.error
  }
}