import axios from "axios";
import VideoResizer from "./video-resizer.js"
import { PlayerId } from "./video-resizer.js"

// Port number is specific to the local-services property in the widget (config.xml)
const ASPort = {
  DOBBY: 9004,
  LOCAL: 9005
};

const HTTPMethod = {
  GET: "get",
  POST: "post"
};

const ASMethod = {
  WATCH_STREAM: "/as/players/1/action/watchstream",
  WATCH_INPUT: "/as/players/1/action/watchinput",
  STOP: "/as/players/1/action/stop"
};

export default class SkyASPlayer {
  constructor(thunderWrapper) {
    this.thunderWrapper = thunderWrapper;
  }

  async watchStream(asParams) {
    VideoResizer.resizeToFullScreen(PlayerId.DEFAULT_AS_PLAYER)
    return await this.httpRequest(
      HTTPMethod.POST,
      ASMethod.WATCH_STREAM,
      asParams
    );
  }

  // TODO: Utilize this one for HDMI cast
  async watchInput(asParams) {
    VideoResizer.resizeToFullScreen(PlayerId.DEFAULT_AS_PLAYER)
    return await this.httpRequest(
      HTTPMethod.POST,
      ASMethod.WATCH_INPUT,
      asParams
    );
  }

  async stop() {
    return await this.httpRequest(HTTPMethod.POST, ASMethod.STOP);
  }

  // Make HTTP request and return whether successful
  async httpRequest(httpMethod, asMethod, asParams) {
    let data = "";
    let url = `http://${this.thunderWrapper.hostIP}:${
      this.thunderWrapper.containerized ? ASPort.DOBBY : ASPort.LOCAL
    }${asMethod}${this.getQueryString(asParams)}`;

    let config = {
      method: httpMethod,
      url: url,
      headers: {},
      data: data
    };

    axios(config)
      .then(function(response) {
        console.log(
          "SkyASPlayer: httpRequest returned " + JSON.stringify(response.data)
        );
      })
      .catch(function(error) {
        console.log(
          "SkyASPlayer: httpRequest returned " + JSON.stringify(error)
        );
        return false;
      });
    return true;
  }

  // Get query string to pass to AS method
  getQueryString(asParams) {
    let queryString = "";
    if (asParams !== undefined) {
      let i = 0;
      for (const key in asParams) {
        if (i === 0) {
          queryString += "?";
        } else {
          queryString += "&";
        }
        queryString += `${key}=${asParams[key]}`;
        i++;
      }
    }
    return queryString;
  }
}
